.news-container {
  position: fixed;
  right: 30px;
  bottom: 30px;
  background-color: #630708;
  width: auto;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  border-radius: 10px;
  color: #e9e8e2;
  font-family: "Inter", sans-serif;
  opacity: 0.8;
  z-index: 100;
}
.news-container h4 {
  font-weight: 700;
  font-size: 20px;
  margin: 0px 20px 10px;
}
.news-container p {
  text-align: center;
  font-weight: 400;
  font-size: 16px;
  margin: 5px 50px;
}
.news-container .news-ok-btn {
  height: 44px;
  width: 146px;
  margin-bottom: 40px;
  border-radius: 100px;
  background: none;
  border: 1.5px #e9e8e2 solid;
  color: #e9e8e2;
  font-family: "Lato", sans-serif;
  font-weight: 700;
  font-size: 16px;
  letter-spacing: 0.3px;
  margin-top: 5%;
  transition: 0.2s ease-out;
  cursor: pointer;
}
.news-container .news-ok-btn:hover {
  opacity: 0.6;
}
.news-container .news-x-btn {
  padding: 20px;
  background-repeat: no-repeat;
  background-position: center;
  position: relative;
  left: 50%;
  top: 0px;
  cursor: pointer;
  background-image: url(../../img/x-white-btn.png);
  width: 14px;
  height: 14px;
  transform: translateX(-50%);
}

@media (max-width: 992px) {
  .news-container {
    left: 5%;
    bottom: 20px;
    width: 90%;
  }
  .news-container h4 {
    font-size: 16px;
  }
  .news-container p {
    font-size: 12px;
    margin: 5px 30px;
  }
}