.values-we-offer-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 444px;
  background-image: url(../../img/values-bg.png);
  z-index: 1;
}
.values-we-offer-container h3 {
  margin-top: 0;
  text-align: center;
  font-size: 24px;
  color: #e9e8e2;
  font-family: "Lato", sans-serif;
  font-weight: 700;
  letter-spacing: 1.6px;
  text-transform: uppercase;
}
.values-we-offer-container .values-box {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 80%;
}
.values-we-offer-container .values-box .value {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: "Inter", sans-serif;
  margin: 72px 58px 0;
}
.values-we-offer-container .values-box .value .value-img {
  position: absolute;
  margin-bottom: 100px;
  background-size: cover;
  transition: 0.2s ease-out;
}
.values-we-offer-container .values-box .value .value-one {
  background-image: url(../../img/rzetelnosc.png);
  background-size: cover;
  height: 84px;
  width: 83px;
}
.values-we-offer-container .values-box .value .value-one:hover {
  height: 93px;
  width: 92px;
  margin-bottom: 110px;
}
.values-we-offer-container .values-box .value .value-two {
  background-image: url(../../img/doswiadczenie.png);
  background-size: cover;
  height: 83px;
  width: 101px;
}
.values-we-offer-container .values-box .value .value-two:hover {
  height: 92px;
  width: 112px;
  margin-bottom: 110px;
}
.values-we-offer-container .values-box .value .value-three {
  background-image: url(../../img/profesjonalizm.png);
  background-size: cover;
  height: 83px;
  width: 102px;
}
.values-we-offer-container .values-box .value .value-three:hover {
  height: 92px;
  width: 111px;
  margin-bottom: 110px;
}
.values-we-offer-container .values-box .value .value-four {
  background-image: url(../../img/terminowosc.png);
  background-size: cover;
  height: 83px;
  width: 75px;
}
.values-we-offer-container .values-box .value .value-four:hover {
  height: 93.1px;
  width: 84px;
  margin-bottom: 110px;
}
.values-we-offer-container .values-box .value .value-five {
  background-image: url(../../img/partnerstwo.png);
  background-size: cover;
  height: 83px;
  width: 100px;
}
.values-we-offer-container .values-box .value .value-five:hover {
  height: 92px;
  width: 110px;
  margin-bottom: 110px;
}
.values-we-offer-container .values-box .value span {
  margin-top: 80px;
  font-size: 19px;
  font-weight: 400;
  font-style: normal;
  color: #e9e8e2;
  line-height: 45.6px;
}

